.tabs {
  height: 40px;
  border-top: 1px solid #AAA;
  border-bottom: 1px solid #AAA;
  .item {
    line-height: 40px;
    font-weight: bold;
    font-size: 2em;
  }
  .item.active {
    text-decoration: underline;
    text-shadow: none;
  }
}

.biography {
  height: 15em;
}

.tab-header {
  display: flex;
  justify-self: center;
  align-self: center;
  width: 100%;
  background-color: $c-black;
  font-family: Aveny;
  height: 1.75em;
  font-size: 1.2em;
  color: $c-white;
  justify-content: space-around;
}

.talents .tab-header {
  margin: 0 0 .5em;
}

.talents input[type="text"] {
  // margin: .25em 0;
  font-family: "Roboto", sans-serif;
}

.tab-item {
  justify-content: center;
  align-items: center;
}

.tab-item.active {
  text-decoration: underline;
  text-shadow: none;
}

// Tab de la hoja items
.sheet-body .tab .editor {
  height: 13em;
}

.itm-sht-tab {
  height: 13em;
}

.tabs .item {
  line-height: 40px;
  font-weight: bold;
  font-size: 2em;
  /* line-height: 40px; */
  /* font-weight: bold; */
  font-size: 1.2em;
  font-family: 'Aveny';
}

.progression {
  display: flex;
  flex-direction: column;
  height: auto;
  width: auto;
}